import React from 'react';
import strings from '../../static/strings';
import Link from '../common/link/Link';
import './Twitter.scss';
import { twitter } from "../../services/twitter";
const twitterServices = new twitter();

class Twitter extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      tweets: []
    };
  }
  
  componentWillMount() {
    twitterServices.get().then((result) => {
      this.setState({
        tweets: result
      })
    });
  }

  render() {
    if (this.state.tweets.length > 0) {
      return (
        <section className="twitter container pt-10">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <h3 className="ui-fw-700 ui-c-dark mb-4"><span className="h1" role="img" aria-label="coffee">‚òïÔ∏è</span> Last tweets</h3>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 ui-bg-white ui-shadow p-0">
              {this.state.tweets.map((tweet, index) => {
                if(index === 0){
                  return (
                    <div key={index}>
                      {tweet.entities.media ? (
                        <div>
                          <div className="twitter__bg-image" style={ {backgroundImage:'url(' + tweet.entities.media[0].media_url_https + ')' }}></div>
                          <div className="p-5">
                            <Link className="ui-c-dark" href={strings.global.links.twitter + '/status/' + tweet.id_str} text={tweet.text} target='_black' />
                          </div>
                        </div>
                      ) : (
                          <div className="p-5">
                            <Link className="h4 ui-c-dark" href={strings.global.links.twitter + '/status/' + tweet.id_str} text={tweet.text} target='_black' />
                          </div>
                      )}
                    </div>
                  )
                }
              })}
            </div>
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 text-center">
              <i className="twitter__icon fab fa-twitter mb-5"></i>
              <Link className="ui-button-green" href={strings.global.links.twitter} text="VIEW MY TWITTER " icon="fas fa-arrow-right" target="_blank" />
            </div>
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 ui-bg-white ui-shadow p-0">
              {this.state.tweets.map(function(tweet, index){
                if(index === 1){
                  return (
                    <div key={index}>
                      {tweet.entities.media ? (
                        <div>
                          <div className="twitter__bg-image" style={ {backgroundImage:'url(' + tweet.entities.media[0].media_url_https + ')' }}></div>
                          <div className="p-5">
                            <Link className="ui-c-dark" href={strings.global.links.twitter + '/status/' + tweet.id_str} text={tweet.text} target='_black' />
                          </div>
                        </div>
                      ) : (
                          <div className="p-5">
                            <Link className="h4 ui-c-dark" href={strings.global.links.twitter + '/status/' + tweet.id_str} text={tweet.text} target='_black' />
                          </div>
                      )}
                    </div>
                  )
                }
              })}
            </div>
          </div>
          <div className="clearfix mt-5 mb-5"></div>
        </section>
      )
    } else {
      return (
        <section className="twitter container pt-5 pb-5">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <h3 className="ui-fw-700 ui-c-dark mb-4"><span className="h1" role="img" aria-label="coffee">‚òïÔ∏è</span> Last tweets</h3>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <div className="h3 mt-5">Loading...</div>
            </div>
          </div>
          <div className="clearfix mt-5 mb-5"></div>
        </section>
      )
    }
  }

};

export default Twitter;
