import React from 'react';
import strings from '../../static/strings';
import Link from '../common/link/Link';
import './Youtube.scss';
import { youtube } from "../../services/youtube";
const youtubeServices = new youtube();

class Youtube extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      videos: []
    };
  }
  
  componentWillMount() {
    youtubeServices.get().then((result) => {
      this.setState({
        videos: result
      })
    });
  }

  render() {
    if (this.state.videos.length > 0) {
      return (
        <section className="youtube container pt-5 pb-5">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <h3 className="ui-fw-700 ui-c-dark mb-4"><span className="h1" role="img" aria-label="video">üìπ</span> Youtube Videos</h3>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 ui-bg-white ui-shadow p-0">
              {this.state.videos.map((video, index) => {
                if(index === 0){
                  return (
                    <div key={index}>
                      <div>
                        <div className="youtube__bg-image" style={ {backgroundImage:'url(' + video.image + ')' }}></div>
                        <div className="p-5">
                          <Link className="ui-c-dark" href={video.url} text={video.title} target='_black' />
                        </div>
                      </div>
                    </div>
                  )
                }
              })}
            </div>
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 text-center">
              <i className="youtube__icon fab fa-youtube mb-5"></i>
              <Link className="ui-button-green" href={strings.global.links.youtube} text="VIEW MY CHANNEL " icon="fas fa-arrow-right" target="_blank" />
            </div>
            <div className="col-xs-12 col-sm-12 col-md-4 col-lg-4 ui-bg-white ui-shadow p-0">
              {this.state.videos.map(function(video, index){
                if(index === 1){
                  return (
                    <div key={index}>
                      <div>
                        <div className="youtube__bg-image" style={ {backgroundImage:'url(' + video.image + ')' }}></div>
                        <div className="p-5">
                          <Link className="ui-c-dark" href={video.url} text={video.title} target='_black' />
                        </div>
                      </div>
                    </div>
                  )
                }
              })}
            </div>
          </div>
          <div className="clearfix mt-5 mb-5"></div>
        </section>
      )
    } else {
      return (
        <section className="youtube container pt-5 pb-5">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
            <h3 className="ui-fw-700 ui-c-dark mb-4"><span className="h1" role="img" aria-label="video">üìπ</span> Youtube Videos</h3>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <div className="h3 mt-5">Loading...</div>
            </div>
          </div>
          <div className="clearfix mt-5 mb-5"></div>
        </section>
      )
    }
  }

};

export default Youtube;
